                        if (payload.__createAsLeaf) {
                            targetArray[newName] = payload.newValue ?? '';
                        }
                        else {
                            targetArray[newName] = [];
                        }
                    }
                }
                replaceVariables(rawData, { type: 'script', script_id: getScriptId() });
                editOpen.value = false;
                editNodeId.value = null;
                editSnapshot.value = null;
                return;
            }
            if (id.startsWith('prefix:')) {
                const idx = Number(id.slice(7));
                if (payload.__delete) {
                    const list = prefixList.value;
                    list.splice(idx, 1);
                    selectedPrefixIndexes.value = new Set(_.chain([...selectedPrefixIndexes.value])
                        .filter(i => i !== idx)
                        .map(i => (i > idx ? i - 1 : i))
                        .value());
                }
                else {
                    const list = prefixList.value;
                    const item = list[idx];
                    if (item && typeof item === 'object') {
                        const oldKey = Object.keys(item)[0];
                        if (payload.newName && payload.newName !== oldKey) {
                            delete item[oldKey];
                            item[payload.newName] = payload.newValue ?? '';
                        }
                        else if (payload.newValue != null) {
                            item[oldKey] = payload.newValue;
                        }
                    }
                }
                replaceVariables(rawData, { type: 'script', script_id: getScriptId() });
                editOpen.value = false;
                editNodeId.value = null;
                editSnapshot.value = null;
                return;
            }
            if (id.startsWith('suffix:')) {
