                    const list = suffixList.value;
                    list.splice(idx, 1);
                    selectedSuffixIndexes.value = new Set(_.chain([...selectedSuffixIndexes.value])
                        .filter(i => i !== idx)
                        .map(i => (i > idx ? i - 1 : i))
                        .value());
                }
                else {
                    const list = suffixList.value;
                    const item = list[idx];
                    if (item && typeof item === 'object') {
                        const oldKey = Object.keys(item)[0];
                        if (payload.newName && payload.newName !== oldKey) {
                            delete item[oldKey];
                            item[payload.newName] = payload.newValue ?? '';
                        }
                        else if (payload.newValue != null) {
                            item[oldKey] = payload.newValue;
