/**
 * 深度遍历与按层提取工具（支持对象与数组）
 *
 * 设计目标：
 * - 仅通过指定“层级（从1开始）”即可获取该层的键名与值
 * - 支持对象/数组混合嵌套
 * - 迭代式（非递归）实现，避免极深层级时的调用栈问题
 */

// TODO： 最后删除
import _ from 'lodash';

export type DeepKey = string | number;

export interface LevelEntry<T = unknown> {
  /** 从根到当前键的路径（包含当前键） */
  path: DeepKey[];
  /** 当前键（对象键或数组下标） */
  key: DeepKey;
  /** 当前键对应的值 */
  value: T;
  /** 层级（从1开始：根对象/数组的第一层子项为1） */
  depth: number;
  /** 父节点（对象或数组） */
  parent: unknown;
  /** 父节点类型 */
  parentType: 'object' | 'array';
}

export interface LevelOptions {
  /** 是否遍历数组（默认 true） */
  traverseArrays?: boolean;
  /** 仅返回叶子节点（值为原始类型或不可继续遍历的值），默认 false */
  leafOnly?: boolean;
}

const defaultOpts: Required<LevelOptions> = {
  traverseArrays: true,
  leafOnly: false,
};

function isTraversable(val: unknown, traverseArrays: boolean): val is Record<string, unknown> | unknown[] {
  if (val == null) return false;
  if (Array.isArray(val)) return traverseArrays;
  return _.isPlainObject(val);
}

/**
 * 获取指定层级的所有键名与值（层级从1开始）
 *
 * 例：
 * root = { a: { b: 1 }, c: 2 }
 * - depth=1 => 键：['a','c']，值：[ {b:1}, 2 ]
 * - depth=2 => 键：['b']，值：[ 1 ]
 */
export function getLevelEntries<T = unknown>(root: unknown, depth: number, options?: LevelOptions): LevelEntry<T>[] {
  const opts = { ...defaultOpts, ...(options || {}) } as Required<LevelOptions>;
  if (!Number.isInteger(depth) || depth < 1) return [];

  // 初始化：从“根”开始，深度=0（根本身不计入层级）
  const queue: Array<{ node: unknown; path: DeepKey[]; depth: number }> = [{ node: root, path: [], depth: 0 }];

  const results: LevelEntry<T>[] = [];

  while (queue.length > 0) {
    const current = queue.shift()!;

    // 若下一层就是目标层，则直接枚举当前节点的子项作为结果
    if (current.depth === depth - 1 && isTraversable(current.node, opts.traverseArrays)) {
      if (Array.isArray(current.node)) {
        for (let i = 0; i < current.node.length; i++) {
          const val = (current.node as unknown[])[i] as T;
          const entry: LevelEntry<T> = {
            path: [...current.path, i],
            key: i,
            value: val,
            depth,
            parent: current.node,
            parentType: 'array',
          };
          if (!opts.leafOnly || !isTraversable(val, opts.traverseArrays)) {
            results.push(entry);
          }
        }
      } else {
        for (const k of Object.keys(current.node as Record<string, unknown>)) {
          const val = (current.node as Record<string, unknown>)[k] as T;
          const entry: LevelEntry<T> = {
            path: [...current.path, k],
            key: k,
            value: val,
            depth,
            parent: current.node,
            parentType: 'object',
          };
          if (!opts.leafOnly || !isTraversable(val, opts.traverseArrays)) {
            results.push(entry);
          }
        }
      }
      // 当前分支已产出目标层结果，无需向下继续
      continue;
    }

    // 若还未抵达目标层，继续向下广度遍历
    if (current.depth < depth - 1 && isTraversable(current.node, opts.traverseArrays)) {
      if (Array.isArray(current.node)) {
        for (let i = 0; i < current.node.length; i++) {
          const val = (current.node as unknown[])[i];
          queue.push({ node: val, path: [...current.path, i], depth: current.depth + 1 });
        }
      } else {
        for (const k of Object.keys(current.node as Record<string, unknown>)) {
          const val = (current.node as Record<string, unknown>)[k];
          queue.push({ node: val, path: [...current.path, k], depth: current.depth + 1 });
        }
      }
    }
  }

  return results;
}

/** 获取指定层级的所有键名（对象键或数组下标） */
export function getLevelKeys(root: unknown, depth: number, options?: LevelOptions): DeepKey[] {
  return getLevelEntries(root, depth, options).map(e => e.key);
}

/** 获取指定层级的所有值 */
export function getLevelValues<T = unknown>(root: unknown, depth: number, options?: LevelOptions): T[] {
  return getLevelEntries<T>(root, depth, options).map(e => e.value);
}

/**
 * 遍历每一层，逐层返回（从1层开始）。
 * 可用于 for...of 简单获取：
 * for (const { depth, entries } of iterateLevels(raw, { maxDepth: 5 })) { ... }
 */
export function* iterateLevels<T = unknown>(
  root: unknown,
  opts?: LevelOptions & { maxDepth?: number },
): Generator<{ depth: number; entries: LevelEntry<T>[] }, void, unknown> {
  const options = { ...defaultOpts, ...(opts || {}) } as Required<LevelOptions> & { maxDepth?: number };
  let depth = 1;
  while (true) {
    if (options.maxDepth && depth > options.maxDepth) return;
    const entries = getLevelEntries<T>(root, depth, options);
    if (entries.length === 0) return; // 没有更多层可遍历时提前结束
    yield { depth, entries };
    depth++;
  }
}

/** 根据路径（由键名/下标组成）在对象中取值 */
export function getByPath<T = unknown>(root: unknown, path: DeepKey[]): T | undefined {
  if (!Array.isArray(path) || path.length === 0) return root as T;

  let current: any = root;
  for (const segment of path) {
    if (current == null) return undefined;
    if (typeof segment === 'number' || typeof segment === 'string') {
      current = current[segment as keyof typeof current];
    } else {
      return undefined;
    }
  }
  return current as T;
}

/** 计算最大可达层级（根的第一层为1） */
export function maxReachableDepth(root: unknown, options?: Pick<LevelOptions, 'traverseArrays'>): number {
  const traverseArrays = options?.traverseArrays ?? true;
  let maxDepth = 0;
  const queue: Array<{ node: unknown; depth: number }> = [{ node: root, depth: 0 }];
  while (queue.length > 0) {
    const { node, depth } = queue.shift()!;
    if (isTraversable(node, traverseArrays)) {
      const nextDepth = depth + 1;
      maxDepth = Math.max(maxDepth, nextDepth);
      if (Array.isArray(node)) {
        for (let i = 0; i < node.length; i++) queue.push({ node: node[i], depth: nextDepth });
      } else {
        for (const k of Object.keys(node as Record<string, unknown>))
          queue.push({ node: (node as any)[k], depth: nextDepth });
      }
    }
  }
  return maxDepth;
}

/** 将路径转为可读字符串（对象用点，数组用中括号） */
export function pathToString(path: DeepKey[]): string {
  if (path.length === 0) return '';
  let out = '';
  for (const seg of path) {
    if (typeof seg === 'number') out += `[${seg}]`;
    else out += (out ? '.' : '') + seg;
  }
  return out;
}

export default {
  getLevelEntries,
  getLevelKeys,
  getLevelValues,
  iterateLevels,
  getByPath,
  maxReachableDepth,
  pathToString,
};
