import { createApp, type App } from 'vue';
import AppComponent from './index.vue';

let currentApp: App | null = null;

const event_type = getButtonEvent('💝快捷指令');
eventOn(event_type, () => {
  start();
});

function start() {
  // 如果已经有运行中的 app，先清理
  if (currentApp) {
    cleanup();
  }

  const $app = createScriptIdDiv();
  $('body').append($app);
  teleportStyle();

  // 每次启动时创建新的 Vue app 实例
  const app = createApp(AppComponent);
  currentApp = app;

  // 定义关闭函数
  const onClose = () => {
    cleanup();
  };

  // 使用 provide 提供 onClose 函数
  app.provide('onClose', onClose);

  app.mount($app[0]);
}

function cleanup() {
  if (currentApp) {
    currentApp.unmount();
    currentApp = null;
  }
  deteleportStyle();
  destroyScriptIdDiv();
}

$(window).on('pagehide', () => {
  cleanup();
});

function teleportStyle() {
  if ($(`head > div[script_id="${getScriptId()}"]`).length > 0) {
    return;
  }
  const $div = $(`<div>`).attr('script_id', getScriptId()).append($(`head > style`, document).clone());
  $('head').append($div);
}

function createScriptIdDiv(): JQuery<HTMLDivElement> {
  return $('<div>').attr('script_id', getScriptId()) as JQuery<HTMLDivElement>;
}

function destroyScriptIdDiv(): void {
  $(`div[script_id="${getScriptId()}"]`).remove();
}

function deteleportStyle() {
  $(`head > div[script_id="${getScriptId()}"]`).remove();
}
