/**
 * quicksend 工具方法集合：抽离纯函数，提升可测试性与复用性
 * 所有注释与命名均使用中文，便于团队理解与维护
 */

export type EditChild = { id: string; newName?: string; newValue?: string };

/** 判断是否为仅含一个键的普通对象 */
export function isSingleKeyObject(v: unknown): v is Record<string, unknown> {
  return !!v && typeof v === 'object' && !Array.isArray(v) && Object.keys(v as Record<string, unknown>).length === 1;
}

/** 将对象的键名从 oldKey 重命名为 newKey（就地修改） */
export function renameObjectKey(obj: Record<string, unknown>, oldKey: string, newKey: string): void {
  if (oldKey === newKey) return;
  if (!(oldKey in obj)) return;
  const val = (obj as any)[oldKey];
  delete (obj as any)[oldKey];
  (obj as any)[newKey] = val;
}

/**
 * 将编辑项应用到任意节点（字符串/数组/单键对象）
 * 与原组件中的逻辑等价，抽离为纯函数
 */
export function applyEditsToNode(node: any, edits: Array<{ id: string; newName?: string; newValue?: string }>): void {
  if (node == null || !edits?.length) return;

  // 对象分支：仅处理 key:* 形式的编辑
  if (typeof node === 'object' && !Array.isArray(node)) {
    for (const e of edits) {
      if (!e) continue;
      if (e.id.startsWith('key:')) {
        const oldKey = e.id.slice(4);
        const newKey = e.newName && e.newName !== oldKey ? e.newName : oldKey;
        if (newKey !== oldKey) renameObjectKey(node as Record<string, unknown>, oldKey, newKey);
        if (e.newValue != null && typeof (node as any)[newKey] === 'string') (node as any)[newKey] = e.newValue;
      }
    }
    return;
  }

  // 数组分支：分组聚合 arridx:*，再逐一应用
  if (Array.isArray(node)) {
    const grouped: Record<number, { newName?: string; newValue?: string }> = {};
    for (const e of edits) {
      if (!e) continue;
      if (e.id.startsWith('arridx:')) {
        const idx = Number(e.id.slice(7));
        grouped[idx] = {
          ...(grouped[idx] || {}),
          ...(e.newName ? { newName: e.newName } : {}),
          ...(e.newValue != null ? { newValue: e.newValue } : {}),
        };
      }
    }
    for (const [k, change] of Object.entries(grouped)) {
      const i = Number(k);
      const el = (node as any[])[i];
      if (typeof el === 'string') {
        if (change.newValue != null) (node as any[])[i] = change.newValue as any;
      } else if (el && typeof el === 'object' && !Array.isArray(el) && isSingleKeyObject(el)) {
        const oldKey = Object.keys(el)[0];
        const val = (el as any)[oldKey];
        const newKey = change.newName && change.newName !== oldKey ? change.newName : oldKey;
        if (newKey !== oldKey) {
          delete (el as any)[oldKey];
          (el as any)[newKey] = val;
        }
        if (change.newValue != null && typeof (el as any)[newKey] === 'string') (el as any)[newKey] = change.newValue;
      }
    }
  }
}

/**
 * 针对定位到的容器与其内部键，按 payload 规则应用编辑
 * 等价于组件中 handleEditSave 内的数组/对象两种分支
 */
export function updateContainerWithPayload(params: {
  container: any;
  keyInContainer: string | number;
  payload: { newName?: string; newValue?: string; children: EditChild[] };
}): void {
  const { container, keyInContainer, payload } = params;

  if (typeof keyInContainer === 'number') {
    const el = container[keyInContainer];
    if (typeof el === 'string') {
      if (payload.newValue != null) container[keyInContainer] = payload.newValue;
      return;
    }
    if (el && typeof el === 'object' && !Array.isArray(el) && isSingleKeyObject(el)) {
      const oldKey = Object.keys(el)[0];
      const newKey = payload.newName && payload.newName !== oldKey ? payload.newName : oldKey;
      const val = (el as any)[oldKey];
      if (newKey !== oldKey) {
        delete (el as any)[oldKey];
        (el as any)[newKey] = val;
      }
      if (payload.newValue != null && typeof (el as any)[newKey] === 'string') (el as any)[newKey] = payload.newValue;
      applyEditsToNode((el as any)[newKey], payload.children);
      return;
    }
    return;
  }

  // 对象容器分支
  const oldKey = String(keyInContainer);
  const newKey = payload.newName && payload.newName !== oldKey ? payload.newName : oldKey;
  if (newKey !== oldKey) renameObjectKey(container as Record<string, unknown>, oldKey, newKey);
  if (payload.newValue != null && typeof (container as any)[newKey] === 'string')
    (container as any)[newKey] = payload.newValue;
  applyEditsToNode((container as any)[newKey], payload.children);
}
