`, "",{"version":3,"sources":["webpack://./src/快捷指令面板/index.vue"],"names":[],"mappings":"AAyzDA,6BACE,eACF,CAEA,oCACE,6DAA8D,CAC9D,eAAgB,CAChB,UAAW,CACX,MACF,CAEA,4BACE,8EACF,CAEA,iCACE,qCAAuC,CACvC,YAAa,CACb,kBAAmB,CACnB,WAAY,CACZ,UACF,CAEA,6CACE,aAAc,CACd,WAAY,CACZ,eAAgB,CAChB,sBAAuB,CACvB,kBACF","sourcesContent":["<template>\n  <div\n    class=\"kakaa-custom-tailwind kk-text-base indicator absolute top-1/2 left-1/2 z-9999 mx-auto flex h-[80dvh] w-[90dvw] max-w-[600px] -translate-x-1/2 -translate-y-1/2 transform flex-col overflow-visible rounded-lg border border-(--quick-send-border-color) bg-(--quick-send-bg) p-0 text-center text-(--quick-send-text) shadow-[0_4px_20px_var(--quick-send-shadow-color)] sm:h-[60vh]\"\n  >\n    <!-- 操作按钮 -->\n    <div class=\"indicator-item flex translate-x-0 -translate-y-1/2 items-center gap-0.5\">\n      <div\n        class=\"flex cursor-pointer items-center justify-center rounded-full bg-(--quick-send-primary-color) p-0.5\"\n        title=\"编辑\"\n        @click=\"handleEdit\"\n      >\n        <template v-if=\"!editMode\">\n          <span class=\"icon-[icon-park-solid--edit]\"></span>\n        </template>\n        <template v-else>\n          <span class=\"icon-[icon-park-solid--correct]\"></span>\n        </template>\n      </div>\n      <div\n        class=\"flex cursor-pointer items-center justify-center rounded-full bg-(--quick-send-primary-color) p-0.5\"\n        title=\"关闭\"\n        @click=\"handleClose\"\n      >\n        <span class=\"icon-[icon-park-solid--error]\"></span>\n      </div>\n    </div>\n\n    <div\n      v-if=\"editMode && showAlertTip\"\n      ref=\"alertTip\"\n      role=\"alert\"\n      class=\"absolute bottom-[3%] left-1/2 z-10001 alert flex w-[90%] -translate-x-1/2 alert-horizontal justify-between border-none bg-(--quick-send-primary-color) py-0.5 alert-info\"\n    >\n      <span class=\"kk-text-base text-(--)\">点击条目或者类别可以编辑内容</span>\n      <div>\n        <div\n          class=\"kk-text-base! rounded-sm bg-(--quick-send-bg) px-0.5 py-0.25 whitespace-nowrap text-(--quick-send-text)\"\n          @click=\"hideAlertTip\"\n        >\n          不再显示\n        </div>\n      </div>\n    </div>\n\n    <div class=\"flex h-full flex-col\">\n      <!-- 主TAB（常驻顶部，样式保持不变） -->\n\n      <div role=\"tablist\" class=\"tabs-border tabs flex items-center overflow-x-auto overflow-y-hidden px-1 pt-0.5 pb-0\">\n        <div class=\"level1Container flex flex-nowrap items-center gap-1\">\n          <div\n            v-for=\"(entry, idx) in level1Entries\"\n            :key=\"makeIdForLevel1Idx(idx)\"\n            class=\"flex flex-nowrap items-center gap-0.5\"\n          >\n            <span v-if=\"editMode\" class=\"drag-handle-l1 shrink-0 cursor-move\" title=\"拖动排序\"> ☰ </span>\n            <span\n              role=\"tab\"\n              class=\"kk-text-lg tab cursor-pointer px-0! whitespace-nowrap text-(--quick-send-text)\"\n              :class=\"{ 'tab-active': activeTabIndex === idx }\"\n              :title=\"entry.label\"\n              @click=\"onClickLevel1(idx)\"\n            >\n              {{ entry.label }}\n            </span>\n          </div>\n          <span\n            v-if=\"editMode\"\n            class=\"ml-1 shrink-0 cursor-pointer rounded-sm border border-dashed border-(--quick-send-text) px-1 py-0\"\n            title=\"新建同级项\"\n            @click.stop=\"openCreate('l1')\"\n          >\n            +\n          </span>\n        </div>\n      </div>\n\n      <!-- 二级TAB（常驻第二行，样式保持不变） -->\n      <div class=\"overflow-x-auto overflow-y-hidden bg-(--quick-send-bg-sec) px-1\">\n        <div class=\"tabs-border tabs flex items-center\" role=\"tablist\">\n          <div ref=\"level2Container\" class=\"level2Container flex flex-nowrap items-center gap-1\">\n            <div\n              v-for=\"(entry, idx) in level2Entries\"\n              :key=\"makeIdForLevel2Idx(idx)\"\n              class=\"flex flex-nowrap items-center gap-0.5\"\n            >\n              <span v-if=\"editMode\" class=\"drag-handle-l2 shrink-0 cursor-move\" title=\"拖动排序\"> ☰ </span>\n              <div\n                role=\"tab\"\n                class=\"kk-text-base tab h-2.5 cursor-pointer px-0! whitespace-nowrap text-(--quick-send-text)\"\n                :class=\"{ 'tab-active': activeSecondIndex === idx }\"\n                @click=\"onClickLevel2(idx)\"\n              >\n                {{ entry.label }}\n              </div>\n            </div>\n            <span\n              v-if=\"editMode\"\n              class=\"ml-1 shrink-0 cursor-pointer rounded-sm border border-dashed border-(--quick-send-text) px-1 py-0\"\n              title=\"新建同级项\"\n              @click.stop=\"openCreate('l2')\"\n            >\n              +\n            </span>\n          </div>\n        </div>\n      </div>\n\n      <!-- 面包屑（位于第二级与按钮之间，仅影响下方按钮集） -->\n      <div class=\"breadcrumbs shrink-0 px-1 pt-0.5\">\n        <ul class=\"pl-0!\">\n          <li\n            v-for=\"(c, idx) in breadcrumbs\"\n            :key=\"`crumb-${activeTabIndex}-${activeSecondIndex}-${idx}-${gridPath.join('-')}`\"\n          >\n            <a v-if=\"idx < breadcrumbs.length - 1\" class=\"cursor-pointer\" @click=\"navigateTo(idx)\">{{ c }}</a>\n            <span v-else>{{ c }}</span>\n          </li>\n        </ul>\n      </div>\n\n      <!-- 按钮集：仅此区域会被替换（从第三级开始） -->\n      <div class=\"mb-0 flex-1 overflow-y-auto px-1\">\n        <div\n          ref=\"gridContainer\"\n          class=\"gridContainer auto-grid grid gap-0.5\"\n          style=\"--auto-grid-min: calc(var(--spacing) * 9)\"\n        >\n          <div\n            v-for=\"(label, idx) in gridLabels\"\n            :key=\"makeIdForGridIdx(idx)\"\n            class=\"auto-grid-item cursor-pointer rounded bg-(--quick-send-bg-sec) px-0.5 py-0.5\"\n            :class=\"{ 'bg-(--quick-send-primary-color)/70': selectedGridIndex === idx }\"\n            :title=\"label\"\n            @click=\"onGridItemClick(idx)\"\n          >\n            <span v-if=\"editMode\" class=\"drag-handle-grid cursor-move px-0.5\" title=\"拖动排序\"> ☰ </span>\n            <span class=\"grid-label\">{{ label }}</span>\n          </div>\n          <div\n            v-if=\"editMode\"\n            class=\"auto-grid-item flex cursor-pointer items-center justify-center rounded border border-dashed px-0.5 py-0.5\"\n            title=\"新建同级项\"\n            @click=\"openCreate('grid')\"\n          >\n            +\n          </div>\n        </div>\n      </div>\n\n      <!-- 底部文本展示与操作区 -->\n      <div\n        class=\"m-1 flex flex-col gap-0.5 rounded border border-(--quick-send-border-color) bg-(--quick-send-bg) p-0.75\"\n      >\n        <!-- 定义可复用的前缀/后缀列表模板 -->\n        <DefinePrefixSuffixList\n          v-slot=\"{\n            list,\n            selectedIndexes,\n            dragHandle,\n            keyPrefix,\n            onItemClick,\n            containerRef,\n            containerClass,\n            onAddClick,\n          }\"\n        >\n          <div class=\"max-h-10 overflow-y-auto rounded bg-(--quick-send-bg-gray-lighter)\">\n            <div\n              :ref=\"containerRef\"\n              :class=\"containerClass\"\n              class=\"auto-grid grid gap-0.5 bg-(--quick-send-bg-gray-lighter)\"\n              style=\"--auto-grid-min: calc(var(--spacing) * 8)\"\n            >\n              <div\n                v-for=\"(item, idx) in list\"\n                :key=\"`${keyPrefix}-${idx}`\"\n                class=\"auto-grid-item kk-text-sm cursor-pointer rounded bg-(--quick-send-bg-sec) p-0.5\"\n                :class=\"{ 'bg-(--quick-send-primary-color)/70': selectedIndexes.has(idx) }\"\n                :title=\"getPrefixSuffixValue(item)\"\n                @click=\"onItemClick(item, idx)\"\n              >\n                <span v-if=\"editMode\" :class=\"dragHandle\" class=\"cursor-move\" title=\"拖动排序\"> ☰ </span>\n                <span class=\"grid-label\"> {{ getPrefixSuffixKey(item) }}</span>\n              </div>\n              <div\n                v-if=\"editMode\"\n                class=\"auto-grid-item kk-text-sm flex cursor-pointer items-center justify-center rounded border border-dashed p-0.5\"\n                title=\"新建项\"\n                @click=\"onAddClick\"\n              >\n                +\n              </div>\n            </div>\n          </div>\n        </DefinePrefixSuffixList>\n\n        <!-- 前缀内容展示区域 -->\n        <div v-if=\"showPrefixList\">\n          <ReusePrefixSuffixList\n            :list=\"prefixList\"\n            :selected-indexes=\"selectedPrefixIndexes\"\n            drag-handle=\"drag-handle-prefix\"\n            container-class=\"prefixContainer\"\n            key-prefix=\"prefix\"\n            :on-item-click=\"onPrefixItemClick\"\n            :container-ref=\"(el: any) => (prefixContainer = el)\"\n            :on-add-click=\"() => openCreatePrefixSuffix('prefix')\"\n          />\n        </div>\n\n        <!-- 后缀内容展示区域 -->\n        <div v-if=\"showSuffixList\">\n          <ReusePrefixSuffixList\n            :list=\"suffixList\"\n            :selected-indexes=\"selectedSuffixIndexes\"\n            drag-handle=\"drag-handle-suffix\"\n            container-class=\"suffixContainer\"\n            key-prefix=\"suffix\"\n            :on-item-click=\"onSuffixItemClick\"\n            :container-ref=\"(el: any) => (suffixContainer = el)\"\n            :on-add-click=\"() => openCreatePrefixSuffix('suffix')\"\n          />\n        </div>\n        <div class=\"flex items-center justify-between\">\n          <div class=\"flex gap-0.5\">\n            <div\n              class=\"flex cursor-pointer items-center gap-0.25 rounded border-none bg-(--quick-send-bg-sec) px-1 py-0.5\"\n              :class=\"{ 'bg-(--quick-send-primary-color)/70': showPrefixList }\"\n              @click=\"togglePrefixList\"\n            >\n              <span class=\"icon-[bxs--left-arrow-square]\"></span>前缀\n            </div>\n            <div\n              class=\"flex cursor-pointer items-center gap-0.25 rounded border-none bg-(--quick-send-bg-sec) px-1 py-0.5\"\n              :class=\"{ 'bg-(--quick-send-primary-color)/70': showSuffixList }\"\n              @click=\"toggleSuffixList\"\n            >\n              <span class=\"icon-[bxs--right-arrow-square]\"></span>后缀\n            </div>\n          </div>\n          <div\n            class=\"flex cursor-pointer items-center gap-0.25 rounded border-none bg-(--quick-send-bg-sec) px-1 py-0.5\"\n            :class=\"{\n              'bg-(--quick-send-primary-color)/70': fixedPrefixSuffixModalOpen,\n              'bg-(--quick-send-primary-color)/30': !fixedPrefixSuffixModalOpen && (fixedPrefix || fixedSuffix),\n            }\"\n            @click=\"openFixedPrefixSuffixModal\"\n          >\n            <span class=\"icon-[bxs--pin]\"></span>固定\n          </div>\n        </div>\n\n        <div class=\"form-control\">\n          <textarea\n            v-model=\"text\"\n            class=\"kk-text-base! box-border min-h-5 w-full resize-y rounded border bg-(--quick-send-bg-gray-lighter)\"\n            :placeholder=\"editMode ? '点击按钮可以编辑内容' : '点击按钮查看内容'\"\n          ></textarea>\n        </div>\n        <div class=\"flex items-center justify-between\">\n          <div class=\"flex flex-wrap gap-0.5\">\n            <span class=\"font-bold whitespace-nowrap\">替换为：</span>\n            <div class=\"flex gap-0.5\">\n              <div class=\"flex items-center gap-0.25\">\n                <input id=\"replace-names-checkbox\" v-model=\"replaceUserNames\" type=\"checkbox\" />\n                <label for=\"replace-names-checkbox\" class=\"cursor-pointer\">user名</label>\n              </div>\n              <div class=\"flex items-center gap-0.25\">\n                <input id=\"replace-char-checkbox\" v-model=\"replaceCharNames\" type=\"checkbox\" />\n                <label for=\"replace-char-checkbox\" class=\"cursor-pointer\">char名</label>\n              </div>\n            </div>\n          </div>\n          <div\n            class=\"cursor-pointer rounded border-none bg-(--quick-send-primary-color) px-1 py-0.5\"\n            :class=\"{ 'pointer-events-none': editMode }\"\n            @click=\"sendQuickText\"\n          >\n            发送\n          </div>\n        </div>\n      </div>\n    </div>\n    <EditKeyModal v-if=\"editOpen\" v-model=\"editOpen\" :snapshot=\"editSnapshot\" @save=\"handleEditSave\" />\n    <FixedPrefixSuffixModal\n      v-if=\"fixedPrefixSuffixModalOpen\"\n      v-model=\"fixedPrefixSuffixModalOpen\"\n      :data=\"fixedPrefixSuffixData\"\n      @save=\"handleFixedPrefixSuffixSave\"\n    />\n  </div>\n</template>\n\n<script lang=\"ts\" setup>\nimport { createReusableTemplate } from '@vueuse/core';\nimport gsap from 'gsap';\nimport { klona } from 'klona';\nimport { computed, inject, nextTick, onBeforeUnmount, onMounted, reactive, ref, toRef, watch } from 'vue';\nimport EditKeyModal, { type EditSnapshot } from './EditKeyModal.vue';\nimport FixedPrefixSuffixModal, { type FixedPrefixSuffixData } from './FixedPrefixSuffixModal.vue';\nimport { type DeepKey, getByPath } from './utils/deep-utils';\nimport { isSingleKeyObject as isSingleKeyObjectUtil, updateContainerWithPayload } from './utils/quicksend';\n\n/**\n * 可复用的前缀/后缀列表模板\n */\nconst [DefinePrefixSuffixList, ReusePrefixSuffixList] = createReusableTemplate<{\n  list: any[];\n  selectedIndexes: Set<number>;\n  dragHandle: string;\n  containerClass?: string;\n  keyPrefix: string;\n  onItemClick: (item: any, idx: number) => void;\n  containerRef?: (el: any) => void;\n  onAddClick?: () => void;\n}>();\n\n/**\n * 是否处于编辑模式\n */\nconst editMode = ref(false);\n\n/**\n * 编辑模态框是否打开\n */\nconst editOpen = ref(false);\n\n/**\n * 编辑快照数据\n */\nconst editSnapshot = ref<EditSnapshot | null>(null);\n\n/**\n * 当前编辑的节点ID\n */\nconst editNodeId = ref<string | null>(null);\n\n/**\n * 原始数据\n */\nconst rawData = reactive(getVariables({ type: 'script', script_id: getScriptId() }));\n\nif (!Array.isArray(rawData.data)) {\n  rawData.data = [];\n}\n\nif (typeof rawData.showTip !== 'boolean') {\n  rawData.showTip = true;\n}\n\n/**\n * 数据：快速发送集\n * 使用 toRef 使其可直接修改，以便 useSortable 可以正常工作\n */\nconst quickSendData = toRef(rawData, 'data');\n\n/**\n * 是否显示提示框\n */\nconst showAlertTip = ref(rawData.showTip);\n\n/**\n * 提示框的 DOM 引用\n */\nconst alertTip = ref<HTMLElement | null>(null);\n\n/**\n * 提示框的定时器\n */\nlet alertTipTimer: ReturnType<typeof setTimeout> | null = null;\n\n/**\n * 当前选中的第一级标签索引\n */\nconst activeTabIndex = ref(0);\n\n/**\n * 当前选中的第二级标签索引\n */\nconst activeSecondIndex = ref(0);\n\n/**\n * 工具：获取\"单键对象\"的键名和值\n *\n * 用于从对象中提取唯一的键值对，主要用于处理快速发送数据结构中的单键对象\n *\n * @param obj - 要检查的对象\n * @returns 如果对象只有一个键，返回包含键名和值的对象；否则返回 undefined\n *\n * @example\n * singleKeyOf({ \"问候语\": \"你好\" })  * singleKeyOf({ \"a\": 1, \"b\": 2 })  * singleKeyOf(null)  */\nfunction singleKeyOf(obj: any): { key: string; value: any } | undefined {\n  if (!obj || typeof obj !== 'object') return undefined;\n  const keys = Object.keys(obj);\n  if (keys.length !== 1) return undefined;\n  const k = keys[0];\n  return { key: k, value: (obj as any)[k] };\n}\n\ntype DataPath = DeepKey[];\n\ninterface Level1Entry {\n  label: string;\n  value: any;\n  path: DataPath;\n  arrIndex: number;\n}\n\nconst level1Entries = computed<Level1Entry[]>(() =>\n  quickSendData.value.map((el: any, idx: number) => {\n    const sk = singleKeyOf(el);\n    if (sk) {\n      return {\n        label: sk.key,\n        value: sk.value,\n        path: [idx, sk.key] as DataPath,\n        arrIndex: idx,\n      };\n    }\n    return {\n      label: typeof el === 'string' ? el : String(idx),\n      value: el,\n      path: [idx] as DataPath,\n      arrIndex: idx,\n    };\n  }),\n);\n\nconst level1Labels = computed<string[]>(() => level1Entries.value.map(entry => entry.label));\n\n/**\n * 标签容器的 DOM 引用\n */\nconst level1Container = ref<HTMLElement | null>(null);\nconst level2Container = ref<HTMLElement | null>(null);\nconst gridContainer = ref<HTMLElement | null>(null);\nconst prefixContainer = ref<HTMLElement | null>(null);\nconst suffixContainer = ref<HTMLElement | null>(null);\n\nconst showPrefixList = ref(false);\nconst showSuffixList = ref(false);\n\nconst prefixList = computed<any[]>(() => (rawData.perfix || rawData.prefix || []) as any[]);\nconst suffixList = computed<any[]>(() => (rawData.suffix || []) as any[]);\n\ntype Level2Entry = {\n  label: string;\n  value: any;\n  arrIndex: number;\n  key: string | null;\n  fromMultiKey: boolean;\n  path: DataPath;\n};\n\nconst level2Entries = computed<Level2Entry[]>(() => {\n  const level1Entry = level1Entries.value[activeTabIndex.value];\n  const arr = level1Entry?.value;\n  if (!Array.isArray(arr)) return [];\n  const basePath = level1Entry?.path ?? ([activeTabIndex.value] as DataPath);\n  const items: Level2Entry[] = [];\n  arr.forEach((el, arrIndex) => {\n    if (el == null) return;\n    const arrPath = [...basePath, arrIndex] as DataPath;\n    if (typeof el === 'object' && !Array.isArray(el)) {\n      const keys = Object.keys(el);\n      if (keys.length === 0) return;\n      if (keys.length === 1) {\n        const key = keys[0];\n        items.push({\n          label: key,\n          value: (el as any)[key],\n          arrIndex,\n          key,\n          fromMultiKey: false,\n          path: [...arrPath, key] as DataPath,\n        });\n        return;\n      }\n      keys.forEach(key => {\n        items.push({\n          label: key,\n          value: (el as any)[key],\n          arrIndex,\n          key,\n          fromMultiKey: true,\n          path: [...arrPath, key] as DataPath,\n        });\n      });\n      return;\n    }\n    items.push({\n      label: typeof el === 'string' ? el : String(arrIndex),\n      value: el,\n      arrIndex,\n      key: null,\n      fromMultiKey: false,\n      path: arrPath,\n    });\n  });\n  return items;\n});\n\nconst level2Labels = computed<string[]>(() => level2Entries.value.map(entry => entry.label));\n\nconst gridLabels = computed(() => gridCurrent.value.labels);\n\nconst gridValues = computed(() => gridCurrent.value.values);\n\n/**\n * 网格当前层级的可编辑数组\n * 用于 VueDraggable 的 v-model 绑定\n */\nconst gridCurrentArray = computed({\n  get: () => {\n    const baseEntry = gridBaseEntry.value;\n    if (!baseEntry) return [];\n    let cur: any = baseEntry.value;\n    for (const idx of gridPath.value) {\n      const step = computeGridEntries(cur, baseEntry.path);\n      cur = step.values[idx];\n    }\n    if (cur == null) return [];\n    return cur;\n  },\n  set: newValue => {\n    const baseEntry = gridBaseEntry.value;\n    if (!baseEntry) return;\n\n    if (gridPath.value.length === 0) {\n      const level1Entry = level1Entries.value[activeTabIndex.value];\n      if (!level1Entry) return;\n\n      const level2Entry = level2Entries.value[activeSecondIndex.value];\n      if (!level2Entry) return;\n\n      if (level2Entry.fromMultiKey && level2Entry.key) {\n        const container = getByPath(quickSendData.value, level2Entry.path.slice(0, -1));\n        if (container) {\n          (container as any)[level2Entry.key] = newValue;\n        }\n      } else if (level2Entry.key) {\n        const arrIdx = level2Entry.arrIndex;\n        const arr = level1Entry.value;\n        if (Array.isArray(arr)) {\n          arr[arrIdx] = { [level2Entry.key]: newValue };\n        }\n      } else {\n        const arr = level1Entry.value;\n        if (Array.isArray(arr)) {\n          arr[level2Entry.arrIndex] = newValue;\n        }\n      }\n    } else {\n      let cur: any = baseEntry.value;\n      for (let i = 0; i < gridPath.value.length - 1; i++) {\n        const idx = gridPath.value[i];\n        const step = computeGridEntries(cur, baseEntry.path);\n        cur = step.values[idx];\n      }\n      const lastIdx = gridPath.value[gridPath.value.length - 1];\n      if (Array.isArray(cur)) {\n        cur[lastIdx] = newValue;\n      } else if (typeof cur === 'object') {\n        const keys = Object.keys(cur);\n        if (lastIdx < keys.length) {\n          (cur as any)[keys[lastIdx]] = newValue;\n        }\n      }\n    }\n\n    replaceVariables(rawData, { type: 'script', script_id: getScriptId() });\n  },\n});\n\n/**\n * 通用的 jQuery Sortable 初始化函数\n *\n * @param selector - jQuery 选择器字符串\n * @param handle - 拖拽手柄的类名\n * @param onUpdate - 更新回调函数，接收 oldIndex 和 newIndex\n */\nfunction createSortable(selector: string, handle: string, onUpdate: (oldIndex: number, newIndex: number) => void) {\n  return () => {\n    $(selector).sortable({\n      handle: handle,\n      cursor: 'move',\n      tolerance: 'pointer',\n      start: function (_event: any, ui: any) {\n        ui.item.data('oldIndex', ui.item.index());\n      },\n      update: function (_event: any, ui: any) {\n        const item = ui.item;\n        const oldIndex = item.data('oldIndex');\n        const newIndex = item.index();\n        if (oldIndex === undefined || newIndex === undefined || oldIndex === newIndex) return;\n\n        // 取消 sortable 的 DOM 更改，让 Vue 来处理\n        $(selector).sortable('cancel');\n\n        // 在 nextTick 中更新数据，确保 Vue 正确渲染\n        nextTick(() => {\n          onUpdate(oldIndex, newIndex);\n        });\n      },\n    });\n  };\n}\n\n/**\n * Level1 排序逻辑\n */\nconst level1Sortable = createSortable('.level1Container', '.drag-handle-l1', (oldIndex, newIndex) => {\n  const arr = [...quickSendData.value];\n  const [movedItem] = arr.splice(oldIndex, 1);\n  arr.splice(newIndex, 0, movedItem);\n\n  rawData.data = arr;\n\n  replaceVariables(klona(rawData), { type: 'script', script_id: getScriptId() });\n});\n\n/**\n * Level2 排序逻辑\n */\nconst level2Sortable = createSortable('.level2Container', '.drag-handle-l2', (oldIndex, newIndex) => {\n  const level1Entry = level1Entries.value[activeTabIndex.value];\n  if (!level1Entry || !Array.isArray(level1Entry.value)) return;\n\n  const arr = [...level1Entry.value];\n  const [movedItem] = arr.splice(oldIndex, 1);\n  arr.splice(newIndex, 0, movedItem);\n\n  const path = level1Entry.path;\n  if (path.length === 1) {\n    rawData.data[path[0] as number] = arr;\n  } else if (path.length === 2) {\n    const arrIdx = path[0] as number;\n    const key = path[1] as string;\n    rawData.data[arrIdx] = { [key]: arr };\n  }\n\n  replaceVariables(klona(rawData), { type: 'script', script_id: getScriptId() });\n});\n\n/**\n * Grid 排序逻辑\n */\nconst gridSortable = createSortable('.gridContainer', '.drag-handle-grid', (oldIndex, newIndex) => {\n  const baseEntry = gridBaseEntry.value;\n  if (!baseEntry) return;\n\n  let cur: any = baseEntry.value;\n  let parent: any = null;\n  let parentKey: number | string | null = null;\n\n  for (let i = 0; i < gridPath.value.length; i++) {\n    const idx = gridPath.value[i];\n    const step = computeGridEntries(cur, baseEntry.path);\n    parent = cur;\n    parentKey = idx;\n    cur = step.values[idx];\n  }\n\n  if (Array.isArray(cur)) {\n    const arr = [...cur];\n    const [movedItem] = arr.splice(oldIndex, 1);\n    arr.splice(newIndex, 0, movedItem);\n\n    if (gridPath.value.length === 0) {\n      gridCurrentArray.value = arr;\n    } else if (parent && parentKey !== null) {\n      if (Array.isArray(parent)) {\n        parent[parentKey as number] = arr;\n      } else if (typeof parent === 'object') {\n        const keys = Object.keys(parent);\n        const key = keys[parentKey as number];\n        if (key) {\n          parent[key] = arr;\n        }\n      }\n    }\n  } else if (typeof cur === 'object' && cur !== null) {\n    const keys = Object.keys(cur);\n    const [movedKey] = keys.splice(oldIndex, 1);\n    keys.splice(newIndex, 0, movedKey);\n\n    const newObj: any = {};\n    for (const key of keys) {\n      newObj[key] = cur[key];\n    }\n\n    if (gridPath.value.length === 0) {\n      gridCurrentArray.value = newObj;\n    } else if (parent && parentKey !== null) {\n      if (Array.isArray(parent)) {\n        parent[parentKey as number] = newObj;\n      } else if (typeof parent === 'object') {\n        const parentKeys = Object.keys(parent);\n        const key = parentKeys[parentKey as number];\n        if (key) {\n          parent[key] = newObj;\n        }\n      }\n    }\n  }\n\n  replaceVariables(klona(rawData), { type: 'script', script_id: getScriptId() });\n});\n\n/**\n * Prefix 排序逻辑\n */\nconst prefixSortable = createSortable('.prefixContainer', '.drag-handle-prefix', (oldIndex, newIndex) => {\n  const list = [...prefixList.value];\n  const [movedItem] = list.splice(oldIndex, 1);\n  list.splice(newIndex, 0, movedItem);\n\n  // 确保更新正确的属性并触发响应式更新\n  if (rawData.perfix) {\n    delete rawData.perfix;\n    rawData.prefix = list;\n  } else {\n    rawData.prefix = list;\n  }\n\n  replaceVariables(klona(rawData), { type: 'script', script_id: getScriptId() });\n});\n\n/**\n * Suffix 排序逻辑\n */\nconst suffixSortable = createSortable('.suffixContainer', '.drag-handle-suffix', (oldIndex, newIndex) => {\n  const list = [...suffixList.value];\n  const [movedItem] = list.splice(oldIndex, 1);\n  list.splice(newIndex, 0, movedItem);\n\n  rawData.suffix = list;\n\n  replaceVariables(klona(rawData), { type: 'script', script_id: getScriptId() });\n});\n\nonMounted(() => {\n  nextTick(() => {\n    level1Sortable();\n    level2Sortable();\n    gridSortable();\n    // 前后缀列表初始状态是隐藏的，不需要在这里初始化\n    // 它们会在 watch(showPrefixList) 和 watch(showSuffixList) 中初始化\n  });\n});\n\n/**\n * 组件卸载时清理 sortable 实例\n */\nonBeforeUnmount(() => {\n  // 只销毁已经初始化的 sortable 实例\n  $('.ui-sortable').each(function () {\n    if ($(this).hasClass('ui-sortable')) {\n      try {\n        $(this).sortable('destroy');\n      } catch (e) {\n        // 忽略销毁时的错误\n      }\n    }\n  });\n});\n\n/**\n * 监听编辑模式变化，启用/禁用拖拽\n */\nwatch(editMode, isEdit => {\n  $('.level1Container').sortable('option', 'disabled', !isEdit);\n  $('.level2Container').sortable('option', 'disabled', !isEdit);\n  $('.gridContainer').sortable('option', 'disabled', !isEdit);\n  // 前后缀的拖拽状态在各自的 watch 中设置\n  if ($('.prefixContainer').hasClass('ui-sortable')) {\n    $('.prefixContainer').sortable('option', 'disabled', !isEdit);\n  }\n  if ($('.suffixContainer').hasClass('ui-sortable')) {\n    $('.suffixContainer').sortable('option', 'disabled', !isEdit);\n  }\n});\n\n/**\n * 监听前缀列表显示状态变化，重新初始化 sortable\n */\nwatch(showPrefixList, isShow => {\n  if (isShow) {\n    nextTick(() => {\n      // 销毁旧实例（如果存在）\n      if ($('.prefixContainer').hasClass('ui-sortable')) {\n        $('.prefixContainer').sortable('destroy');\n      }\n      // 重新初始化\n      prefixSortable();\n      // 根据当前编辑模式状态设置是否可拖拽\n      $('.prefixContainer').sortable('option', 'disabled', !editMode.value);\n    });\n  }\n});\n\n/**\n * 监听后缀列表显示状态变化，重新初始化 sortable\n */\nwatch(showSuffixList, isShow => {\n  if (isShow) {\n    nextTick(() => {\n      // 销毁旧实例（如果存在）\n      if ($('.suffixContainer').hasClass('ui-sortable')) {\n        $('.suffixContainer').sortable('destroy');\n      }\n      // 重新初始化\n      suffixSortable();\n      // 根据当前编辑模式状态设置是否可拖拽\n      $('.suffixContainer').sortable('option', 'disabled', !editMode.value);\n    });\n  }\n});\n\n/**\n * 第三级及更深按钮集：类型驱动下钻\n * 从第三级开始的“原地下钻”路径\n */\n/**\n * 网格导航路径\n *\n * 记录从第三级开始的导航路径，用于实现多层级下钻导航\n */\nconst gridPath = ref<number[]>([]);\n\nconst gridBaseEntry = computed<Level2Entry | null>(() => level2Entries.value[activeSecondIndex.value] ?? null);\n\ntype GridEntryMeta = { path: DataPath };\n\nfunction computeGridEntries(\n  node: any,\n  basePath: DataPath,\n): { labels: string[]; values: any[]; metas: GridEntryMeta[] } {\n  if (node == null) return { labels: [], values: [], metas: [] };\n\n  if (typeof node === 'object' && !Array.isArray(node)) {\n    const keys = Object.keys(node as Record<string, any>);\n    return {\n      labels: keys,\n      values: keys.map(k => (node as any)[k]),\n      metas: keys.map(k => ({ path: [...basePath, k] as DataPath })),\n    };\n  }\n\n  if (Array.isArray(node)) {\n    const labels: string[] = [];\n    const values: any[] = [];\n    const metas: GridEntryMeta[] = [];\n    node.forEach((el, idx) => {\n      const arrPath = [...basePath, idx] as DataPath;\n      const sk = singleKeyOf(el);\n      if (sk) {\n        labels.push(sk.key);\n        values.push(sk.value);\n        metas.push({ path: [...arrPath, sk.key] as DataPath });\n        return;\n      }\n      if (el && typeof el === 'object' && !Array.isArray(el)) {\n        const keys = Object.keys(el);\n        if (keys.length === 0) return;\n        keys.forEach(key => {\n          labels.push(key);\n          values.push((el as any)[key]);\n          metas.push({ path: [...arrPath, key] as DataPath });\n        });\n        return;\n      }\n      labels.push(typeof el === 'string' ? el : String(idx));\n      values.push(el);\n      metas.push({ path: arrPath });\n    });\n    return { labels, values, metas };\n  }\n  return { labels: [], values: [], metas: [] };\n}\n\nconst gridCurrent = computed(() => {\n  const baseEntry = gridBaseEntry.value;\n  if (!baseEntry) return { labels: [], values: [], metas: [] };\n  let cur: any = baseEntry.value;\n  let curPath: DataPath = baseEntry.path;\n  for (const idx of gridPath.value) {\n    const step = computeGridEntries(cur, curPath);\n    const meta = step.metas[idx];\n    if (!meta) return { labels: [], values: [], metas: [] };\n    cur = step.values[idx];\n    curPath = meta.path;\n  }\n  return computeGridEntries(cur, curPath);\n});\n\n/**\n * 当前显示的文本内容\n */\nconst text = ref('');\n\n/**\n * 是否替换名字\n */\nconst replaceUserNames = ref((rawData.replaceNames as any)?.user ?? false);\nconst replaceCharNames = ref((rawData.replaceNames as any)?.char ?? false);\n\n/**\n * 原始文本内容（未经宏替换处理）\n */\nconst originalText = ref('');\n\n/**\n * 替换用户名和角色名为宏，并进行宏替换\n *\n * 当复选框被勾选时，将文本中的用户名和角色名替换为 {{user}} 和 {{char}} 宏\n * 然后对文本进行一次 substitudeMacros 转义\n */\nfunction applyMacroSubstitution() {\n  let processedText = originalText.value;\n\n  const userName = SillyTavern.name1;\n  const charName = SillyTavern.name2;\n  if (replaceUserNames.value && userName) {\n    processedText = processedText.replaceAll('user', userName);\n  }\n\n  if (replaceCharNames.value && charName) {\n    processedText = processedText.replaceAll('character(s)', charName);\n  }\n\n  // 根据当前已选前缀/后缀与固定前后缀，自动拼接展示文本（让文本框中也可见固定前后缀）\n  const buildSelectedConcat = (list: any[], selected: Set<number>) => {\n    let out = '';\n    list.forEach((item, idx) => {\n      if (selected.has(idx)) {\n        const v = getPrefixSuffixValue(item);\n        if (v) out += v;\n      }\n    });\n    return out;\n  };\n\n  const prefixConcat = buildSelectedConcat(prefixList.value, selectedPrefixIndexes.value);\n  const suffixConcat = buildSelectedConcat(suffixList.value, selectedSuffixIndexes.value);\n  // 固定前后缀需要出现在最外层，保持与发送时一致的可视效果\n  const fixedPre = fixedPrefix.value || '';\n  const fixedSuf = fixedSuffix.value || '';\n  text.value = `${fixedPre}${prefixConcat}${processedText}${suffixConcat}${fixedSuf}`;\n}\n\n/**\n * 监听原始文本变化，自动应用宏替换\n */\nwatch(originalText, () => {\n  applyMacroSubstitution();\n});\n\n/**\n * 监听复选框变化，自动应用宏替换并保存到数据\n */\nwatch([replaceUserNames, replaceCharNames], () => {\n  applyMacroSubstitution();\n\n  if (!rawData.replaceNames) {\n    rawData.replaceNames = {};\n  }\n  (rawData.replaceNames as any).user = replaceUserNames.value;\n  (rawData.replaceNames as any).char = replaceCharNames.value;\n\n  replaceVariables(rawData, { type: 'script', script_id: getScriptId() });\n});\n\n/**\n * 当前选中的网格项索引\n */\nconst selectedGridIndex = ref<number | null>(null);\n\n/**\n * 当前选中的前缀项索引集合（支持多选）\n */\nconst selectedPrefixIndexes = ref<Set<number>>(new Set());\n\n/**\n * 当前选中的后缀项索引集合（支持多选）\n */\nconst selectedSuffixIndexes = ref<Set<number>>(new Set());\n\nif (Array.isArray(rawData.selectedPrefixSuffix) && rawData.selectedPrefixSuffix.length > 0) {\n  const saved = rawData.selectedPrefixSuffix[0];\n  if (saved) {\n    if (Array.isArray(saved.prefix)) {\n      selectedPrefixIndexes.value = new Set(saved.prefix);\n    }\n    if (Array.isArray(saved.suffix)) {\n      selectedSuffixIndexes.value = new Set(saved.suffix);\n    }\n  }\n}\n\nwatch(\n  [selectedPrefixIndexes, selectedSuffixIndexes],\n  () => {\n    // 选中项改变时，更新展示文本并持久化选中状态\n    applyMacroSubstitution();\n    if (!Array.isArray(rawData.selectedPrefixSuffix)) {\n      rawData.selectedPrefixSuffix = [{ prefix: [], suffix: [] }];\n    }\n\n    if (rawData.selectedPrefixSuffix.length === 0) {\n      rawData.selectedPrefixSuffix.push({ prefix: [], suffix: [] });\n    }\n\n    rawData.selectedPrefixSuffix[0] = {\n      prefix: Array.from(selectedPrefixIndexes.value),\n      suffix: Array.from(selectedSuffixIndexes.value),\n    };\n\n    replaceVariables(rawData, { type: 'script', script_id: getScriptId() });\n  },\n  { deep: true },\n);\n\n/**\n * 固定前后缀弹窗是否打开\n */\nconst fixedPrefixSuffixModalOpen = ref(false);\n\n/**\n * 固定前缀内容\n */\nconst fixedPrefix = ref('');\n\n/**\n * 固定后缀内容\n */\nconst fixedSuffix = ref('');\n\n/**\n * 监听固定前后缀变化，使文本框实时反映固定前后缀\n */\nwatch([fixedPrefix, fixedSuffix], () => {\n  applyMacroSubstitution();\n});\n\n/**\n * 固定前后缀数据（传递给弹窗组件）\n */\nconst fixedPrefixSuffixData = computed<FixedPrefixSuffixData>(() => ({\n  prefix: fixedPrefix.value,\n  suffix: fixedSuffix.value,\n}));\n\n// 从存储中加载固定前后缀\nif (rawData.fixedPrefixSuffix) {\n  fixedPrefix.value = rawData.fixedPrefixSuffix.prefix || '';\n  fixedSuffix.value = rawData.fixedPrefixSuffix.suffix || '';\n}\n\n/**\n * 打开固定前后缀弹窗\n */\nfunction openFixedPrefixSuffixModal() {\n  fixedPrefixSuffixModalOpen.value = true;\n}\n\n/**\n * 处理固定前后缀保存\n */\nfunction handleFixedPrefixSuffixSave(result: FixedPrefixSuffixData) {\n  fixedPrefix.value = result.prefix;\n  fixedSuffix.value = result.suffix;\n\n  if (!rawData.fixedPrefixSuffix) {\n    rawData.fixedPrefixSuffix = { prefix: '', suffix: '' };\n  }\n\n  rawData.fixedPrefixSuffix.prefix = result.prefix;\n  rawData.fixedPrefixSuffix.suffix = result.suffix;\n\n  replaceVariables(rawData, { type: 'script', script_id: getScriptId() });\n  // 更新可视文本，确保文本框立即显示固定前后缀\n  applyMacroSubstitution();\n}\n\n/**\n * 关闭回调函数\n *\n * 从父组件注入的关闭函数，用于关闭快速发送界面\n */\nconst onClose = inject<() => void>('onClose');\n\n/**\n * 处理关闭操作\n *\n * 优先调用注入的 onClose 回调函数，如果没有则触发 close 事件\n * 用于关闭快速发送界面\n */\nconst handleClose = () => {\n  if (onClose) onClose();\n};\n\n/**\n * 处理编辑模式切换\n *\n * 切换编辑模式状态，在编辑模式和查看模式之间切换\n * 编辑模式下可以修改快速发送数据的内容\n */\nconst handleEdit = () => {\n  editMode.value = !editMode.value;\n};\n\n/**\n * 隐藏提示框并保存设置\n */\nconst hideAlertTip = () => {\n  if (alertTipTimer) {\n    clearTimeout(alertTipTimer);\n    alertTipTimer = null;\n  }\n\n  if (alertTip.value) {\n    gsap.to(alertTip.value, {\n      y: 100,\n      opacity: 0,\n      duration: 0.3,\n      ease: 'power2.in',\n      onComplete: () => {\n        showAlertTip.value = false;\n        rawData.showTip = false;\n        replaceVariables(klona(rawData), { type: 'script', script_id: getScriptId() });\n      },\n    });\n  } else {\n    showAlertTip.value = false;\n    rawData.showTip = false;\n    replaceVariables(klona(rawData), { type: 'script', script_id: getScriptId() });\n  }\n};\n\n/**\n * 监听 editMode 变化，控制提示框的显示\n */\nwatch(editMode, async newVal => {\n  if (newVal && rawData.showTip) {\n    showAlertTip.value = true;\n\n    await nextTick();\n\n    if (alertTip.value) {\n      gsap.fromTo(alertTip.value, { y: 100, opacity: 0 }, { y: 0, opacity: 1, duration: 0.4, ease: 'power2.out' });\n\n      if (alertTipTimer) {\n        clearTimeout(alertTipTimer);\n      }\n      alertTipTimer = setTimeout(() => {\n        if (alertTip.value) {\n          gsap.to(alertTip.value, {\n            y: 100,\n            opacity: 0,\n            duration: 0.3,\n            ease: 'power2.in',\n            onComplete: () => {\n              showAlertTip.value = false;\n            },\n          });\n        }\n      }, 100000);\n    }\n  } else {\n    if (alertTipTimer) {\n      clearTimeout(alertTipTimer);\n      alertTipTimer = null;\n    }\n    showAlertTip.value = false;\n  }\n});\n\n/**\n * 处理第一级标签点击事件\n *\n * 当用户点击第一级标签时触发，在非编辑模式下切换到对应的标签，在编辑模式下打开编辑弹窗\n *\n * @param idx - 被点击的标签索引\n */\nfunction onClickLevel1(idx: number) {\n  if (editMode.value) {\n    openEdit(idx, 'l1');\n    return;\n  }\n  selectLevel1(idx);\n}\n\n/**\n * 处理第二级标签点击事件\n *\n * 当用户点击第二级标签时触发，在非编辑模式下切换到对应的标签，在编辑模式下打开编辑弹窗\n *\n * @param idx - 被点击的标签索引\n */\nfunction onClickLevel2(idx: number) {\n  if (editMode.value) {\n    openEdit(idx, 'l2');\n    return;\n  }\n  selectLevel2(idx);\n}\n\n/**\n * 处理网格项点击事件\n *\n * 当用户点击网格中的按钮时触发，在非编辑模式下处理点击逻辑，在编辑模式下打开编辑弹窗\n *\n * @param idx - 被点击的网格项索引\n */\nfunction onGridItemClick(idx: number) {\n  if (editMode.value) {\n    openEdit(idx, 'grid');\n    return;\n  }\n  onGridEntryClick(idx);\n}\n\n/**\n * 提取节点的文本内容\n *\n * 检查节点是否为字符串类型，如果是则返回该字符串，否则返回 undefined\n * 用于判断节点是否包含可直接显示的文本内容\n *\n * @param node - 要检查的节点\n * @returns 如果是字符串则返回该字符串，否则返回 undefined\n */\nfunction nodeContent(node: any): string | undefined {\n  return typeof node === 'string' ? node : undefined;\n}\n\n/**\n * 选择第一级标签\n *\n * 切换到指定的第一级标签，并重置第二级标签和网格路径\n * 同时清除当前选中的网格项\n *\n * @param idx - 要选择的第一级标签索引\n */\nfunction selectLevel1(idx: number) {\n  if (idx === activeTabIndex.value) return;\n  activeTabIndex.value = idx;\n  activeSecondIndex.value = 0;\n  gridPath.value = [];\n  selectedGridIndex.value = null;\n}\n\n/**\n * 选择第二级标签\n *\n * 切换到指定的第二级标签，并重置网格路径\n *\n * @param idx - 要选择的第二级标签索引\n */\nfunction selectLevel2(idx: number) {\n  if (idx === activeSecondIndex.value) return;\n  activeSecondIndex.value = idx;\n  gridPath.value = [];\n  selectedGridIndex.value = null;\n}\n\n/**\n * 处理网格条目点击\n *\n * 当用户点击网格中的条目时，如果条目是字符串则显示在文本框中\n * 如果条目是对象或数组，则进入下一层导航\n *\n * @param idx - 被点击的网格条目索引\n */\nfunction onGridEntryClick(idx: number) {\n  selectedGridIndex.value = idx;\n  const entry = gridValues.value[idx];\n  const c = nodeContent(entry);\n  if (c != null) {\n    originalText.value = c;\n    appendSillyTavernText();\n    return;\n  }\n  gridPath.value = [...gridPath.value, idx];\n  selectedGridIndex.value = 0;\n}\n\n/**\n * 面包屑导航路径\n *\n * 根据当前选中的第一级、第二级标签和网格路径生成面包屑导航\n * 用于显示当前在数据层级中的位置\n */\nconst breadcrumbs = computed(() => {\n  const out: string[] = [];\n  const l1 = level1Labels.value[activeTabIndex.value];\n  if (l1) out.push(l1);\n  const l2 = level2Labels.value[activeSecondIndex.value];\n  if (l2 != null) out.push(l2);\n  const baseEntry = gridBaseEntry.value;\n  if (!baseEntry) return out;\n  let cur: any = baseEntry.value;\n  let curPath: DataPath = baseEntry.path;\n  for (const idx of gridPath.value) {\n    const step = computeGridEntries(cur, curPath);\n    if (idx >= 0 && idx < step.labels.length) out.push(step.labels[idx]);\n    const meta = step.metas[idx];\n    if (!meta) break;\n    cur = step.values[idx];\n    curPath = meta.path;\n  }\n  return out;\n});\n\n/**\n * 导航到指定层级\n *\n * 根据面包屑索引导航到对应的层级，用于实现面包屑导航功能\n * 在编辑模式下不执行导航操作\n *\n * @param idx - 面包屑索引，0和1对应第一级和第二级，2及以上对应网格层级\n */\nfunction navigateTo(idx: number) {\n  if (editMode.value) return;\n  selectedGridIndex.value = null;\n  if (idx < 0) {\n    gridPath.value = [];\n    return;\n  }\n  if (idx <= 1) {\n    gridPath.value = [];\n    return;\n  }\n  gridPath.value = gridPath.value.slice(0, idx - 2);\n}\n\n/**\n * 监听第一级标签变化\n *\n * 当第一级标签数组长度变化时，确保当前选中的索引不超出范围\n */\nwatch(level1Labels, list => {\n  if (activeTabIndex.value >= list.length) activeTabIndex.value = 0;\n});\n\n/**\n * 监听第二级标签变化\n *\n * 当第二级标签数组长度变化时，确保当前选中的索引不超出范围\n */\nwatch(level2Labels, list => {\n  if (activeSecondIndex.value >= list.length) activeSecondIndex.value = 0;\n});\n\n/**\n * 创建子节点的编辑快照\n *\n * 为编辑模态框创建子节点的快照数据，包含节点的基本信息\n * 支持对象和数组两种类型的节点处理\n *\n * @param node - 要创建快照的节点\n * @returns 子节点快照数组\n */\nfunction makeChildrenSnapshot(node: any): EditSnapshot['children'] {\n  const items: EditSnapshot['children'] = [];\n  if (node == null) return items;\n\n  if (typeof node === 'object' && !Array.isArray(node)) {\n    for (const k of Object.keys(node)) {\n      const v = (node as any)[k];\n      items.push({\n        id: `key:${k}`,\n        name: k,\n        canRename: true,\n        isLeaf: typeof v === 'string',\n        value: typeof v === 'string' ? v : undefined,\n      });\n    }\n  } else if (Array.isArray(node)) {\n    node.forEach((el, i) => {\n      if (typeof el === 'string') {\n        items.push({ id: `arridx:${i}`, name: `#${i}`, canRename: false, isLeaf: true, value: el });\n      } else if (el && typeof el === 'object' && !Array.isArray(el) && isSingleKeyObjectUtil(el)) {\n        const k = Object.keys(el)[0];\n        const v = (el as any)[k];\n        items.push({\n          id: `arridx:${i}`,\n          name: k,\n          canRename: true,\n          isLeaf: typeof v === 'string',\n          value: typeof v === 'string' ? v : undefined,\n        });\n      } else {\n        items.push({ id: `arridx:${i}`, name: `#${i}`, canRename: false, isLeaf: false });\n      }\n    });\n  }\n  return items;\n}\n\n/**\n * 路径段类型定义\n *\n * 用于描述数据路径中的每个段，包含类型和键名\n */\nfunction makeIdFromPath(path: DataPath): string {\n  return JSON.stringify(path);\n}\n\nfunction parseId(id: string): DataPath {\n  if (!id || id.startsWith('prefix:') || id.startsWith('suffix:')) return [];\n  try {\n    const parsed = JSON.parse(id);\n    return Array.isArray(parsed) ? (parsed as DataPath) : [];\n  } catch (error) {\n    return [];\n  }\n}\n\nfunction resolveById(\n  root: any,\n  id: string,\n): {\n  container: any;\n  keyInContainer: string | number;\n  title: string;\n  value: any;\n  canRename: boolean;\n  isLeaf: boolean;\n  path: DataPath;\n} | null {\n  const path = parseId(id);\n  if (path.length === 0) return null;\n\n  const containerPath = path.slice(0, -1);\n  const lastKey = path[path.length - 1];\n  const container = containerPath.length === 0 ? root : getByPath(root, containerPath);\n  if (container == null) return null;\n\n  if (Array.isArray(container) && typeof lastKey === 'number') {\n    const element = container[lastKey];\n\n    if (typeof element === 'string') {\n      return {\n        container,\n        keyInContainer: lastKey,\n        title: `#${lastKey}`,\n        value: element,\n        canRename: false,\n        isLeaf: true,\n        path,\n      };\n    }\n    if (element && typeof element === 'object' && !Array.isArray(element) && isSingleKeyObjectUtil(element)) {\n      const innerKey = Object.keys(element)[0];\n      const innerValue = (element as any)[innerKey];\n      return {\n        container: element,\n        keyInContainer: innerKey,\n        title: innerKey,\n        value: innerValue,\n        canRename: true,\n        isLeaf: typeof innerValue === 'string',\n        path,\n      };\n    }\n    return {\n      container,\n      keyInContainer: lastKey,\n      title: `#${lastKey}`,\n      value: element,\n      canRename: false,\n      isLeaf: typeof element === 'string',\n      path,\n    };\n  }\n\n  const value = getByPath(root, path);\n  const title = typeof lastKey === 'string' ? lastKey : typeof lastKey === 'number' ? `#${lastKey}` : String(lastKey);\n  const canRename = typeof lastKey === 'string';\n\n  return {\n    container,\n    keyInContainer: lastKey as string | number,\n    title,\n    value,\n    canRename,\n    isLeaf: typeof value === 'string',\n    path,\n  };\n}\nfunction makeIdForLevel1Idx(idx: number): string {\n  const entry = level1Entries.value[idx];\n  const path = entry?.path ?? ([idx] as DataPath);\n  return makeIdFromPath(path);\n}\n\nfunction makeIdForLevel2Idx(idx: number): string {\n  const entry = level2Entries.value[idx];\n  if (!entry) return makeIdForLevel1Idx(activeTabIndex.value);\n  return makeIdFromPath(entry.path);\n}\n\nfunction makeIdForGridIdx(idx: number): string {\n  const baseEntry = level2Entries.value[activeSecondIndex.value];\n  if (!baseEntry) return makeIdForLevel1Idx(activeTabIndex.value);\n  let current: any = baseEntry.value;\n  let currentPath: DataPath = baseEntry.path;\n  for (const stepIdx of gridPath.value) {\n    const step = computeGridEntries(current, currentPath);\n    const meta = step.metas[stepIdx];\n    if (!meta) return makeIdFromPath(currentPath);\n    current = step.values[stepIdx];\n    currentPath = meta.path;\n  }\n  const step = computeGridEntries(current, currentPath);\n  const targetMeta = step.metas[idx];\n  return makeIdFromPath(targetMeta ? targetMeta.path : currentPath);\n}\nfunction openEdit(idx: number, scope: 'l1' | 'l2' | 'grid') {\n  const id =\n    scope === 'grid' ? makeIdForGridIdx(idx) : scope === 'l2' ? makeIdForLevel2Idx(idx) : makeIdForLevel1Idx(idx);\n  const resolved = resolveById(quickSendData.value, id);\n  if (!resolved) return;\n\n  editNodeId.value = id;\n  editSnapshot.value = {\n    title: resolved.title,\n    name: resolved.title,\n    canRename: resolved.canRename,\n    isLeaf: resolved.isLeaf,\n    value: resolved.isLeaf ? (resolved.value as string) : undefined,\n    children: makeChildrenSnapshot(resolved.value),\n  };\n  editOpen.value = true;\n}\n\n/**\n * 打开创建新项的模态框\n *\n * @param scope - 创建项所在的层级\n */\nfunction openCreate(scope: 'l1' | 'l2' | 'grid') {\n  editNodeId.value = `__create__:${scope}`;\n  editSnapshot.value = {\n    title: '新建项',\n    name: '',\n    canRename: true,\n    isLeaf: false,\n    value: undefined,\n    children: [],\n    isCreate: true,\n    createLevel: scope,\n  };\n  editOpen.value = true;\n}\n\n/**\n * 处理编辑保存\n *\n * 将编辑模态框中的修改应用到实际数据中\n * 支持修改节点名称、值和子节点，并处理不同类型的容器（数组和对象）\n * 支持删除节点及其所有内部层级数据\n *\n * @param payload - 编辑保存的数据，包含新名称、新值和子节点修改，或删除标记\n */\nfunction handleEditSave(payload: {\n  newName?: string;\n  newValue?: string;\n  children: Array<{ id: string; newName?: string; newValue?: string }>;\n  __delete?: boolean;\n  __create?: boolean;\n  __createAsLeaf?: boolean;\n}) {\n  const id = editNodeId.value;\n  if (!id) return;\n\n  if (id.startsWith('__create__:')) {\n    const scope = id.slice(11);\n    const newName = payload.newName || '新项';\n\n    if (scope === 'prefix') {\n      const newItem = { [newName]: payload.newValue ?? '' };\n      if (!rawData.prefix && !rawData.perfix) {\n        rawData.prefix = [];\n      }\n      const list = rawData.perfix || rawData.prefix;\n      if (Array.isArray(list)) {\n        list.push(newItem);\n      }\n    } else if (scope === 'suffix') {\n      const newItem = { [newName]: payload.newValue ?? '' };\n      if (!rawData.suffix) {\n        rawData.suffix = [];\n      }\n      rawData.suffix.push(newItem);\n    } else if (scope === 'l1') {\n      const newItem = { [newName]: [] };\n      quickSendData.value.push(newItem);\n    } else if (scope === 'l2') {\n      const level1Entry = level1Entries.value[activeTabIndex.value];\n      if (level1Entry && Array.isArray(level1Entry.value)) {\n        const newItem = { [newName]: [] };\n        level1Entry.value.push(newItem);\n      }\n    } else if (scope === 'grid') {\n      const baseEntry = gridBaseEntry.value;\n      if (!baseEntry) return;\n\n      let targetArray: any = baseEntry.value;\n      for (const idx of gridPath.value) {\n        const step = computeGridEntries(targetArray, baseEntry.path);\n        targetArray = step.values[idx];\n      }\n\n      if (Array.isArray(targetArray)) {\n        const newItem = payload.__createAsLeaf ? { [newName]: payload.newValue ?? '' } : { [newName]: [] };\n        targetArray.push(newItem);\n      } else if (typeof targetArray === 'object' && targetArray !== null) {\n        if (payload.__createAsLeaf) {\n          (targetArray as any)[newName] = payload.newValue ?? '';\n        } else {\n          (targetArray as any)[newName] = [];\n        }\n      }\n    }\n\n    replaceVariables(rawData, { type: 'script', script_id: getScriptId() });\n    editOpen.value = false;\n    editNodeId.value = null;\n    editSnapshot.value = null;\n    return;\n  }\n\n  if (id.startsWith('prefix:')) {\n    const idx = Number(id.slice(7));\n    if (payload.__delete) {\n      const list = prefixList.value;\n      list.splice(idx, 1);\n      selectedPrefixIndexes.value = new Set(\n        _.chain([...selectedPrefixIndexes.value])\n          .filter(i => i !== idx)\n          .map(i => (i > idx ? i - 1 : i))\n          .value(),\n      );\n    } else {\n      const list = prefixList.value;\n      const item = list[idx];\n      if (item && typeof item === 'object') {\n        const oldKey = Object.keys(item)[0];\n        if (payload.newName && payload.newName !== oldKey) {\n          delete (item as any)[oldKey];\n          (item as any)[payload.newName] = payload.newValue ?? '';\n        } else if (payload.newValue != null) {\n          (item as any)[oldKey] = payload.newValue;\n        }\n      }\n    }\n    replaceVariables(rawData, { type: 'script', script_id: getScriptId() });\n    editOpen.value = false;\n    editNodeId.value = null;\n    editSnapshot.value = null;\n    return;\n  }\n\n  if (id.startsWith('suffix:')) {\n    const idx = Number(id.slice(7));\n    if (payload.__delete) {\n      const list = suffixList.value;\n      list.splice(idx, 1);\n      selectedSuffixIndexes.value = new Set(\n        _.chain([...selectedSuffixIndexes.value])\n          .filter(i => i !== idx)\n          .map(i => (i > idx ? i - 1 : i))\n          .value(),\n      );\n    } else {\n      const list = suffixList.value;\n      const item = list[idx];\n      if (item && typeof item === 'object') {\n        const oldKey = Object.keys(item)[0];\n        if (payload.newName && payload.newName !== oldKey) {\n          delete (item as any)[oldKey];\n          (item as any)[payload.newName] = payload.newValue ?? '';\n        } else if (payload.newValue != null) {\n          (item as any)[oldKey] = payload.newValue;\n        }\n      }\n    }\n    replaceVariables(rawData, { type: 'script', script_id: getScriptId() });\n    editOpen.value = false;\n    editNodeId.value = null;\n    editSnapshot.value = null;\n    return;\n  }\n\n  const resolved = resolveById(quickSendData.value, id);\n  if (!resolved) return;\n  const { container, keyInContainer } = resolved;\n\n  if (payload.__delete) {\n    if (Array.isArray(container)) {\n      container.splice(keyInContainer as number, 1);\n    } else if (typeof container === 'object') {\n      delete (container as any)[keyInContainer];\n    }\n  } else {\n    updateContainerWithPayload({ container, keyInContainer, payload });\n  }\n\n  replaceVariables(rawData, { type: 'script', script_id: getScriptId() });\n\n  editOpen.value = false;\n  editNodeId.value = null;\n  editSnapshot.value = null;\n}\n\nfunction togglePrefixList() {\n  showPrefixList.value = !showPrefixList.value;\n  if (showPrefixList.value) showSuffixList.value = false;\n}\n\nfunction toggleSuffixList() {\n  showSuffixList.value = !showSuffixList.value;\n  if (showSuffixList.value) showPrefixList.value = false;\n}\n\nfunction getPrefixSuffixKey(item: any): string {\n  if (item && typeof item === 'object' && !Array.isArray(item)) {\n    const keys = Object.keys(item);\n    if (keys.length > 0) return keys[0];\n  }\n  return '';\n}\n\nfunction getPrefixSuffixValue(item: any): string {\n  if (item && typeof item === 'object' && !Array.isArray(item)) {\n    const keys = Object.keys(item);\n    if (keys.length > 0) {\n      const value = item[keys[0]];\n      return typeof value === 'string' ? value : String(value);\n    }\n  }\n  return '';\n}\n\n/**\n * 点击前缀项时的处理（支持多选）\n *\n * @param item - 被点击的前缀项\n * @param idx - 前缀项索引\n */\nfunction onPrefixItemClick(item: any, idx: number) {\n  if (editMode.value) {\n    openEditPrefixSuffix(idx, 'prefix');\n    return;\n  }\n\n  if (selectedPrefixIndexes.value.has(idx)) {\n    selectedPrefixIndexes.value.delete(idx);\n  } else {\n    selectedPrefixIndexes.value.add(idx);\n  }\n\n  selectedPrefixIndexes.value = new Set(selectedPrefixIndexes.value);\n  // 仅改变选中集合，文本由 applyMacroSubstitution 自动拼接\n  applyMacroSubstitution();\n}\n\n/**\n * 点击后缀项时的处理（支持多选）\n *\n * @param item - 被点击的后缀项\n * @param idx - 后缀项索引\n */\nfunction onSuffixItemClick(item: any, idx: number) {\n  if (editMode.value) {\n    openEditPrefixSuffix(idx, 'suffix');\n    return;\n  }\n\n  if (selectedSuffixIndexes.value.has(idx)) {\n    selectedSuffixIndexes.value.delete(idx);\n  } else {\n    selectedSuffixIndexes.value.add(idx);\n  }\n\n  selectedSuffixIndexes.value = new Set(selectedSuffixIndexes.value);\n  // 仅改变选中集合，文本由 applyMacroSubstitution 自动拼接\n  applyMacroSubstitution();\n}\n\nfunction openEditPrefixSuffix(idx: number, type: 'prefix' | 'suffix') {\n  const list = type === 'prefix' ? prefixList.value : suffixList.value;\n  const item = list[idx];\n  if (!item) return;\n  const key = getPrefixSuffixKey(item);\n  const value = getPrefixSuffixValue(item);\n  editNodeId.value = `${type}:${idx}`;\n  editSnapshot.value = {\n    title: key,\n    name: key,\n    canRename: true,\n    isLeaf: true,\n    value: value,\n    children: [],\n  };\n  editOpen.value = true;\n}\n\n/**\n * 打开创建前缀/后缀项的模态框\n *\n * @param type - 创建的类型，'prefix' 或 'suffix'\n */\nfunction openCreatePrefixSuffix(type: 'prefix' | 'suffix') {\n  editNodeId.value = `__create__:${type}`;\n  editSnapshot.value = {\n    title: type === 'prefix' ? '新建前缀' : '新建后缀',\n    name: '',\n    canRename: true,\n    isLeaf: true,\n    value: '',\n    children: [],\n    isCreate: true,\n  };\n  editOpen.value = true;\n}\n\nfunction sendQuickText() {\n  const finalText = text.value;\n\n  triggerSlash(`/send ${finalText}|/trigger`);\n  if (onClose) onClose();\n}\n\n/**\n * 将酒馆已有文本附加到最后\n */\nfunction appendSillyTavernText() {\n  const STText = $('#send_textarea').val();\n  if (STText === '' || STText === null) return;\n  nextTick(() => {\n    text.value = text.value + '\\n\\n' + STText;\n  });\n}\n</script>\n\n<style lang=\"css\" scoped>\n.tab-active {\n  font-weight: 700;\n}\n\n.tab-active:before {\n  --tab-border-color: var(--quick-send-primary-color) !important;\n  border-top: none;\n  width: 100%;\n  left: 0;\n}\n\n.auto-grid {\n  grid-template-columns: repeat(auto-fit, minmax(var(--auto-grid-min, 2.5rem), 1fr));\n}\n\n.auto-grid-item {\n  min-width: var(--auto-grid-min, 2.5rem);\n  display: flex;\n  align-items: center;\n  gap: 0.25rem;\n  width: 100%;\n}\n\n.auto-grid-item .grid-label {\n  flex: 1 1 auto;\n  min-width: 0;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  white-space: nowrap;\n}\n</style>\n\n<style>\n@import './index.css';\n\n:root {\n  --quick-send-primary-color: var(--SmartThemeQuoteColor);\n  --quick-send-bg: var(--SmartThemeBlurTintColor);\n  --quick-send-bg-sec: var(--SmartThemeBorderColor);\n  --quick-send-text: var(--SmartThemeBodyColor);\n  --quick-send-border-color: var(--SmartThemeBorderColor);\n  --quick-send-shadow-color: var(--SmartThemeShadowColor);\n}\n</style>\n"],"sourceRoot":""}]);
